@import "helper";
@import '~antd/dist/antd.css';
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*:after,*:before{
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: #000;
}
ul,ol{
  list-style: none;
  margin-bottom: 0;
}
button,input{
  font:inherit;
}
:focus{
  outline: none;
}
h1,h2,h3,h4,h5,h6{
  font-weight: normal;
}

body{
  font-family: $font-hei;
  font-size: 16px;
  line-height: 1.2;
  background-color: #FBF4F4;
}

.icon{
  width: 1.5em;
  height: 1.5em;
}

.oneLine {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

@media screen and (min-width: 500px) {
  .tags{
    display: none!important;
  }
}
